export const reportClaimType = (
  type: PlansTypeKeys | "all"
): ClaimTypesEnum | "all" => {
  let result: ClaimTypesEnum | "all" = "all";
  switch (type) {
    case PlansTypeKeys.APPLICATION:
      result = ClaimTypesEnum.application;
      break;
    case PlansTypeKeys.DOCUMENT_VALIDATION:
      result = ClaimTypesEnum.document;
      break;
    case PlansTypeKeys.EXAMINATION:
      result = ClaimTypesEnum.exam;
      break;
    case PlansTypeKeys.MARKS_PUBLICATION:
      result = ClaimTypesEnum.marks_publication;
      break;
    case PlansTypeKeys.PLACEMENT_TRANSFER:
      result = ClaimTypesEnum.placement;
      break;
    default:
      result = "all";
      break;
  }

  return result;
};

const searchData = (
  objList: ClaimToPrintInterface[],
  text: string
): ClaimToPrintInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "names" ||
        prop === "gender" ||
        prop === "national_id" ||
        prop === "phone" ||
        prop === "email" ||
        prop === "claim_title" ||
        prop === "claim_type" ||
        prop === "claim_level" ||
        prop === "education_category" ||
        prop === "claim_session" ||
        prop === "date" ||
        prop === "decision"
      ) {
        if (product[prop]) {
          flag =
            product[prop]!.toString()
              .toLowerCase()
              .indexOf(text.toLowerCase()) > -1;
        }
      }
      if (flag) break;
    }
    return flag;
  });
};

