import React from "react";
import VBarChart from "../ChartComponents/VBarChart";

type AllClaimChartProps = {
  ClaimTypes: {
    title: string;
    count: {
      all: number;
      accepted: number;
      rejected: number;
      pending: number;
    };
    type: string | "all";
  }[];
};

const AllClaimChart: React.FC<AllClaimChartProps> = (props) => {
  const FC_GetAllClaimChartData = (): {
    accepted: number[];
    rejected: number[];
    active: number[];
    labels: string[];
  } => {
    let acceptedArray: number[] = [],
      rejectedArray: number[] = [],
      activeArray: number[] = [],
      labelArray: string[] = [];

    for (const typeKey in props.ClaimTypes) {
      if (props.ClaimTypes[typeKey].type !== "all") {
        acceptedArray.push(props.ClaimTypes[typeKey].count.accepted);
        rejectedArray.push(props.ClaimTypes[typeKey].count.rejected);
        activeArray.push(props.ClaimTypes[typeKey].count.pending);
        labelArray.push(props.ClaimTypes[typeKey].type.replace(/_/g, " "));
      }
    }
    return {
      accepted: acceptedArray,
      rejected: rejectedArray,
      active: activeArray,
      labels: labelArray,
    };
  };

  return (
    <section className="my-3 max-w-full bg-white">
      <div className="flex items-center justify-between px-3">
        <span className="text-gray-600 font-semibold text-xl py-4 px-0">
          Claim chart on country level
        </span>
      </div>
      <div className="mb-4 pb-2 px-3">
        <VBarChart
          labels={FC_GetAllClaimChartData().labels}
          categoriesLabel={FC_GetAllClaimChartData().labels}
          dataLabels={false}
          yaxisText="(Applicants)"
          tooltipText="Claims"
          series={[
            {
              name: "Pending Claim",
              data: FC_GetAllClaimChartData().active,
            },
            {
              name: "Accepted Claim",
              data: FC_GetAllClaimChartData().accepted,
            },
            {
              name: "Rejected Claim",
              data: FC_GetAllClaimChartData().rejected,
            },
          ]}
          colors={["#4b5563", "#469769", "#d97724"]}
          defaultBar={{
            borderRadius: 4,
            horizontal: false,
            columnWidth: "95%",
            endingShape: "rounded",
          }}
          stacked={true}
        />
      </div>
    </section>
  );
};

export default React.memo(AllClaimChart);
