import { ApexOptions } from "apexcharts";
import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";

type VBarCharPropstInterface = {
  labels: string[];
  categoriesLabel: any;
  bar?: {
    borderRadius: number;
    horizontal: boolean;
    columnWidth: string;
    endingShape: string;
  };
  dataLabels: boolean;
  yaxisText: string;
  tooltipText: string;
  series: {
    name: string;
    data: number[];
  }[];
  defaultBar?: {
    borderRadius: number;
    horizontal: boolean;
    columnWidth: string;
    endingShape: string;
  };
  colors?: any[];
  title?: {
    text: string;
    align: string;
    margin: number;
    offestY: number;
    style: {
      fontSize: string;
    };
  };
  stacked?: boolean;
  FC_LoadSelected?: (labelIndex: number, seriesIndex: number) => void;
  chartHeight?: number;
};

const defaultBar = {
  borderRadius: 4,
  horizontal: false,
  columnWidth: "95%",
  endingShape: "rounded",
};

const VBarChart: React.FC<VBarCharPropstInterface> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [options, setOptions] = useState<ApexOptions>({
    chart: {
      events: {
        dataPointSelection: (event, chartContext, config) => {
          console.log({
            labelClicked: config.w.config.labels[config.dataPointIndex],
            // seriesClicked: config.w.config.series[config.dataPointIndex],
          });
          if (props.FC_LoadSelected) {
            props.FC_LoadSelected(config.dataPointIndex, config.seriesIndex);
          }
          // console.log(config.seriesIndex);
          // console.log(config.dataPointIndex);
        },
      },
      stacked: props.stacked ? props.stacked : false,
    },
    plotOptions: {
      bar: props.defaultBar ? props.defaultBar : defaultBar,
    },
    labels: props.labels,
    dataLabels: {
      enabled: true,
    },
    xaxis: {
      labels: {
        rotate: -45,
      },
      categories: props.categoriesLabel,
      tickPlacement: "on",
    },
    yaxis: {
      title: {
        text: props.yaxisText,
      },
    },
    fill: {
      opacity: 1,
    },
    tooltip: {
      y: {
        formatter: function (val: number) {
          return val + " " + props.tooltipText;
        },
      },
    },
    grid: {
      padding: {
        left: 30, // or whatever value that works
        right: 30, // or whatever value that works
      },
    },
    noData: {
      text: "No data found",
      align: "center",
      verticalAlign: "middle",
    },
  });

  useEffect(() => {
    if (props.colors && props.colors.length > 0 && loading) {
      setOptions({
        chart: {
          stacked: props.stacked ? props.stacked : false,
          events: {
            dataPointSelection: (event, chartContext, config) => {
              console.log({
                chartClicked: config.w.config.labels[config.dataPointIndex],
              });
              if (props.FC_LoadSelected) {
                props.FC_LoadSelected(
                  config.dataPointIndex,
                  config.seriesIndex
                );
              }
            },
          },
          zoom: {
            enabled: true,
            autoScaleYaxis: true,
          },
        },
        plotOptions: {
          bar: props.defaultBar ? props.defaultBar : defaultBar,
        },
        labels: props.labels,
        dataLabels: {
          enabled: true,
        },
        xaxis: {
          labels: {
            rotate: -45,
          },
          categories: props.categoriesLabel,
          tickPlacement: "on",
        },
        yaxis: {
          title: {
            text: props.yaxisText,
          },
        },
        fill: {
          opacity: 1,
        },
        tooltip: {
          y: {
            formatter: function (val: number) {
              return val + " " + props.tooltipText;
            },
          },
        },
        grid: {
          padding: {
            left: 30, // or whatever value that works
            right: 30, // or whatever value that works
          },
        },
        noData: {
          text: "No data found",
          align: "center",
          verticalAlign: "middle",
        },
        colors: props.colors,
      });
    } else if (loading) {
      setOptions({
        chart: {
          stacked: props.stacked ? props.stacked : false,
          events: {
            dataPointSelection: (event, chartContext, config) => {
              console.log({
                labelClicked: config.w.config.labels[config.dataPointIndex],
                // seriesClicked: config.w.config.series[config.dataPointIndex],
              });
              if (props.FC_LoadSelected) {
                props.FC_LoadSelected(
                  config.dataPointIndex,
                  config.seriesIndex
                );
              }
              // console.log(config.seriesIndex);
              // console.log(config.dataPointIndex);
            },
          },
          zoom: {
            enabled: true,
            autoScaleYaxis: true,
          },
        },
        plotOptions: {
          bar: props.defaultBar ? props.defaultBar : defaultBar,
        },
        labels: props.labels,
        dataLabels: {
          enabled: true,
        },
        xaxis: {
          labels: {
            rotate: -45,
          },
          categories: props.categoriesLabel,
          tickPlacement: "on",
        },
        yaxis: {
          title: {
            text: props.yaxisText,
          },
        },
        fill: {
          opacity: 1,
        },
        tooltip: {
          y: {
            formatter: function (val: number) {
              return val + " " + props.tooltipText;
            },
          },
        },
        grid: {
          padding: {
            left: 30, // or whatever value that works
            right: 30, // or whatever value that works
          },
        },
        noData: {
          text: "No data found",
          align: "center",
          verticalAlign: "middle",
        },
      });
    }
    setLoading(false);
  }, [loading, options, props]);

  if (loading) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }

  return (
    <div className="w-full">
      <Chart
        options={options}
        series={props.series}
        type="bar"
        width="100%"
        height={props.chartHeight ? props.chartHeight : undefined}
      />
    </div>
  );
};

export default React.memo(VBarChart);
