import React from "react";
import Chart from "react-apexcharts";

type chartType = {
  options: {
    labels: string[];
    legend?: ApexLegend;
    plotOptions?: ApexPlotOptions;
    colors?: any[];
  };
  series: number[];
  width?: number;
};

const PieChart: React.FC<chartType> = (props) => {
  return (
    <div className="donut">
      <Chart
        options={props.options}
        series={props.series}
        type="donut"
        width={`${props.width ? props.width : "380"}`}
      />
    </div>
  );
};

export default React.memo(PieChart);
