import React, { Component } from "react";
import { FaBookOpen } from "react-icons/fa";
import { connect } from "react-redux";
import {
  Auth,
  schoolLevelsStoreInterface,
  getAllClaims,
  GetClaimingTypes,
  getAllApplications,
  loadEducationCategories,
  setSelectedClaimType,
  PushPopupAlert,
} from "../../../actions";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
} from "../../../actions/document";
import { GetAllSessionPlans } from "../../../actions/session";
import { FC_GetPositionsByInstitution } from "../../../actions/settings";
import { PopupAlertData } from "../../../components/Alert/Popup";
import {
  SessionPlanInterface,
  PositionAPI,
  education_category,
  PlansTypeKeys,
  CandidateClaimInterface,
  ClaimToPrintInterface,
  ClaimDecisionStatus,
} from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { ClaimingReducerType } from "../../../reducers/claiming";
import { DocumentReducerInterface } from "../../../reducers/document";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import { DATE } from "../../../utils/functions";
import { reportClaimType } from "../../Admin/Claiming/ClaimingReport";
import { adminDisplayClaimTitle } from "../../Claimings/Admin";
import NoSession from "../../SessionSettings/NoSession";
import PieChart from "../ChartComponents/PieChart";
import ExamDashboardLabels from "../ExamDashboard/ExamDashboardLabels";
import AllClaimChart from "./AllClaimChart";
import ClaimDashBoardTitle from "./ClaimDashBoardTitle";

interface WaitingListDashboardProps {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  systemPositions: PositionAPI[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  setSelectedClaimType: Function;
  FC_GetPositionsByInstitution: Function;
  getAllClaims: Function;
  GetClaimingTypes: Function;
  getAllApplications: Function;
  GetAllSessionPlans: Function;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface WaitingListDashboardStates {
  thisPeriod: string;
  loading: boolean;
  loadingSession: boolean;
  periodChanged: boolean;
  loadingPosition: boolean;
  loadingApplications: boolean;
  loadingClaims: boolean;
  loadingDocuments: boolean;
  loadingInstitutes: boolean;
  loadingClarifications: boolean;
  loadingFields: boolean;
  thisEducation: education_category | null;
  thisLabel: PlansTypeKeys | "all";
}
class _ClaimDashboard extends Component<
  WaitingListDashboardProps,
  WaitingListDashboardStates
> {
  constructor(props: WaitingListDashboardProps) {
    super(props);

    this.state = {
      thisPeriod: "",
      loading: true,
      loadingSession: true,
      periodChanged: false,
      loadingPosition: true,
      loadingApplications: true,
      loadingClaims: true,
      loadingDocuments: true,
      loadingInstitutes: true,
      loadingClarifications: true,
      loadingFields: true,
      thisEducation: null,
      thisLabel: "all",
    };
  }

  setLoadingInstitutes = (value: boolean) => {
    this.setState({ loadingInstitutes: value });
  };

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingSession = (value: boolean) => {
    this.setState({ loadingSession: value });
  };

  setPeriodChanged = (value: boolean) => {
    this.setState({ periodChanged: value });
  };

  setLoadingPosition = (value: boolean) => {
    this.setState({ loadingPosition: value });
  };

  setLoadingApplications = (value: boolean) => {
    this.setState({ loadingApplications: value });
  };

  setLoadingClaims = (value: boolean) => {
    this.setState({ loadingClaims: value });
  };
  setLoadingDocuments = (value: boolean) => {
    this.setState({ loadingDocuments: value });
  };

  setLoadingClarifications = (value: boolean) => {
    this.setState({ loadingClarifications: value });
  };

  setLoadingFields = (value: boolean) => {
    this.setState({ loadingFields: value });
  };

  setThisEducation = (value: education_category | null) => {
    this.setState({ thisEducation: value });
  };

  setThisLabel = (value: PlansTypeKeys | "all") => {
    this.setState({ thisLabel: value });
  };

  setThisPeriod = (value: string) => {
    this.setState({ thisPeriod: value });
  };

  componentDidMount = () => {
    // LOADING EDUCATION CATEGORY =================================
    if (
      this.props.schoolLevels.education_categories === null ||
      this.props.schoolLevels.education_categories.length === 0
    ) {
      this.props.loadEducationCategories(this.setLoadingInstitutes);
    } else {
      this.setLoadingInstitutes(false);
    }
    // END LOADING EDUCATION CATEGORY =================================

    // LOADING APPLICATION SESSION ====================================
    if (
      this.props.application_sessions === null ||
      this.props.application_sessions.length === 0
    ) {
      this.props.GetAllSessionPlans(
        (status: boolean, msg: string, data?: SessionPlanInterface[]) => {
          if (!status) {
            this.setLoadingSession(false);
            this.setLoading(false);
            if (msg.length) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            if (data && data.length > 0) {
              let thisSession = data.find((item) => item.is_active === true);
              this.setThisPeriod(
                thisSession ? thisSession._id : data[data.length - 1]._id
              );
              this.onPeriodChange(
                thisSession ? thisSession._id : data[data.length - 1]._id
              );
            }
          }
        }
      );
    } else if (this.state.loadingSession) {
      this.setLoading(false);
      this.setLoadingSession(false);
    }

    // SETTING ACTIVE SESSION ==========================
    if (
      isEmptyOrSpaces(this.state.thisPeriod) &&
      !this.state.loadingSession &&
      this.props.application_sessions &&
      this.props.application_sessions.length > 0
    ) {
      let thisSession = this.props.application_sessions.find(
        (item) => item.is_active === true
      );
      this.setThisPeriod(
        thisSession
          ? thisSession._id
          : this.props.application_sessions[
              this.props.application_sessions.length - 1
            ]._id
      );
      this.onPeriodChange(
        thisSession
          ? thisSession._id
          : this.props.application_sessions[
              this.props.application_sessions.length - 1
            ]._id
      );
    }

    // LOADING DOCUMENT CATEGORIES ==========================
    if (
      !this.props.documentReducer.documentCategory &&
      this.state.loadingDocuments
    ) {
      this.props.getDocumentCategory();
      this.setLoadingDocuments(false);
    } else if (this.state.loadingDocuments) {
      this.setLoadingDocuments(false);
    }
    // LOADING DOCUMENT CLARIFICATIONS ==========================
    if (
      !this.props.documentReducer.documentSubCategory &&
      this.state.loadingClarifications
    ) {
      this.props.getDocumentSubCategory();
      this.setLoadingClarifications(false);
    } else if (this.state.loadingClarifications) {
      this.setLoadingClarifications(false);
    }
    // LOADING DOCUMENT FIELDS ==========================
    if (
      !this.props.documentReducer.documentFields &&
      this.state.loadingFields
    ) {
      this.props.getDocumentFields();
      this.setLoadingFields(false);
    } else if (this.state.loadingFields) {
      this.setLoadingFields(false);
    }

    // LOADING CLAIMING OF CURRENT CANDIDATES ================================

    // LOADING POSITIONS ==============================================
    if (
      this.props.systemPositions === null &&
      this.props.auth.education_institution_id
    ) {
      this.props.FC_GetPositionsByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string) => {
          this.setLoadingPosition(status);
          console.log({ status: status, msg: msg });
          if (msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else if (this.state.loadingPosition) {
      this.setLoadingPosition(false);
    }
  };

  onPeriodChange = (period_id: string) => {
    this.setPeriodChanged(true);
    this.props.getAllClaims(2, period_id, (status: boolean, msg: string) => {
      !status && this.setPeriodChanged(false);
      if (status === false && msg.length) {
        this.props.PushPopupAlert({
          title: msg,
          theme: PopupAlertTheme.danger,
        });
      }
    });

    this.props.getAllApplications(period_id, (status: boolean, msg: string) => {
      !status && this.setLoadingApplications(status);
      if (status === false && msg.length) {
        this.props.PushPopupAlert({
          title: msg,
          theme: PopupAlertTheme.danger,
        });
      }
    });
  };

  getClaimInstitute = (thisClaim: CandidateClaimInterface): string | null => {
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      return null;
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (this.props.claiming.allApplications) {
        let thisApplication = this.props.claiming.allApplications.find(
          (item) => item._id === thisClaim.claiming_on_id
        );
        if (
          thisApplication &&
          this.props.claiming.allApplications &&
          this.props.systemPositions
        ) {
          // return thisApplication.
          let thisPosition = this.props.systemPositions.find(
            (item) => item.position_id === thisApplication!.position
          );
          if (thisPosition) {
            return thisPosition.education_institute_id.education_institute_id;
          }
        }
      }
    }

    return null;
  };

  displayClaimTitle = (thisClaim: CandidateClaimInterface): string => {
    let result: string = thisClaim.claiming_on_id;
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      result =
        thisClaim.user.details.filter(
          (item) => item.validation_status !== "waiting"
        ).length > 0
          ? adminDisplayClaimTitle(
              this.props.documentReducer,
              thisClaim.user.details.filter(
                (item) => item.validation_status !== "waiting"
              ),
              thisClaim
            )
          : "Document not found";
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (this.props.claiming.allApplications) {
        let thisApplication = this.props.claiming.allApplications.find(
          (item) => item._id === thisClaim.claiming_on_id
        );
        if (
          thisApplication &&
          this.props.claiming.allApplications &&
          this.props.systemPositions
        ) {
          let thisPosition = this.props.systemPositions.find(
            (item) => item.position_id === thisApplication!.position
          );
          if (thisPosition) {
            result = thisPosition.position_title;
          } else {
            result = "Application not found";
          }
        }
      }
    }

    return result;
  };

  ClaimTypes = (
    claims: ClaimToPrintInterface[]
  ): {
    title: string;
    count: {
      all: number;
      accepted: number;
      rejected: number;
      pending: number;
    };
    type: PlansTypeKeys | "all";
  }[] => {
    let output: {
      title: string;
      count: {
        all: number;
        accepted: number;
        rejected: number;
        pending: number;
      };
      type: PlansTypeKeys | "all";
    }[] = [
      {
        title: "All claim types",
        count: {
          all: claims.length,
          accepted: claims.filter(
            (item) => item.decision === ClaimDecisionStatus.ACCEPTED
          ).length,
          rejected: claims.filter(
            (item) => item.decision === ClaimDecisionStatus.REJECTED
          ).length,
          pending: claims.filter((item) => item.decision === "PENDING").length,
        },
        type: "all",
      },
    ];

    Object.values(PlansTypeKeys).forEach((PlansKey) => {
      output.push({
        title: capitalizeFirstLetter(reportClaimType(PlansKey)),
        count: {
          all: claims.filter((itm) => itm.claim_type === PlansKey).length,
          accepted: claims.filter(
            (itm) =>
              itm.claim_type === PlansKey &&
              itm.decision === ClaimDecisionStatus.ACCEPTED
          ).length,
          rejected: claims.filter(
            (itm) =>
              itm.claim_type === PlansKey &&
              itm.decision === ClaimDecisionStatus.REJECTED
          ).length,
          pending: claims.filter(
            (itm) => itm.claim_type === PlansKey && itm.decision === "PENDING"
          ).length,
        },
        type: PlansKey,
      });
    });
    return output;
  };

  FC_ClaimData = (
    allClaims: CandidateClaimInterface[] | null
  ): ClaimToPrintInterface[] => {
    if (allClaims === null || allClaims.length <= 0) return [];
    let arrayNew: ClaimToPrintInterface[] = [];
    let n: number = 1;
    // LOOP IN ALL CLAIM ONE BY ONE ###########################
    for (const key in allClaims) {
      if (Object.prototype.hasOwnProperty.call(allClaims, key)) {
        // LOOP IN ALL CLAIM OF ONE FILE ONE BY ONE ===========================
        for (const keyClaim in allClaims[key].claims) {
          if (
            Object.prototype.hasOwnProperty.call(
              allClaims[key].claims,
              keyClaim
            )
          ) {
            const element = allClaims[key].claims[keyClaim];
            let dataArray: ClaimToPrintInterface = {
              claim_id: n,
              names:
                allClaims[key].user.user_info.fname +
                " " +
                allClaims[key].user.user_info.lname,
              gender: allClaims[key].user.user_info.sex ? "Male" : "Female",
              national_id: allClaims[key].user.n_id,
              phone: allClaims[key].user.phone_number,
              email: allClaims[key].user.email,
              claim_type: allClaims[key].type_key,
              claim_level: element.level,
              education_category: this.getClaimInstitute(allClaims[key]),
              claim_session: this.state.thisPeriod,
              date: DATE(element.createdAt),
              decision: element.decision_taken_status
                ? element.decision_taken_status
                : "PENDING",
              claim_title: this.displayClaimTitle(allClaims[key]),
            };
            arrayNew.push(dataArray);
            n++;
          }
        }
      }
    }
    return arrayNew;
  };

  getClaimDataByTitle = (
    allData: {
      title: string;
      count: {
        all: number;
        accepted: number;
        rejected: number;
        pending: number;
      };
      type: PlansTypeKeys | "all";
    }[]
  ):
    | {
        title: string;
        count: {
          all: number;
          accepted: number;
          rejected: number;
          pending: number;
        };
        type: PlansTypeKeys | "all";
      }
    | undefined => {
    return allData.find((item) => item.type === this.state.thisLabel);
  };

  render() {
    if (
      this.props.application_sessions &&
      this.props.application_sessions.length === 0
    ) {
      return <NoSession />;
    }

    if (
      this.state.loading ||
      this.props.application_sessions === null ||
      this.props.claiming.allClaims === null
    ) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    return (
      <div>
        {/* HEADER TITLE */}
        <ClaimDashBoardTitle
          education_categories={this.props.schoolLevels.education_categories}
          loading={this.state.loading}
          loadingApplications={this.state.loadingApplications}
          loadingEducation={this.state.loadingInstitutes}
          thisEducation={this.state.thisEducation}
          setThisEducation={this.setThisEducation}
          thisPeriod={this.state.thisPeriod}
          setThisPeriod={this.setThisPeriod}
          application_sessions={this.props.application_sessions}
          reportTitle={"Claim Dashboard"}
          allEducation={true}
          periodChanged={this.state.periodChanged}
          onPeriodChange={this.onPeriodChange}
        />
        {/* END HEADER TITLE */}

        {/* ===================================================================================== */}

        {/* CLAIM ALL STATUS LABELS ======================================= */}
        <section className="my-2">
          <div className="text-2xl font-semibold text-gray-600">
            Claim type:
            <select
              className={`bg-white text-gray-600 border-2 border-blue-600 rounded-lg px-2 py-1 focus:outline-none`}
              value={this.state.thisLabel}
              onChange={(e) =>
                this.setThisLabel(e.target.value as PlansTypeKeys | "all")
              }
            >
              {this.ClaimTypes(
                this.FC_ClaimData(this.props.claiming.allClaims)!
              ).map((item, index: number) => (
                <option key={item.type + index} value={item.type}>
                  {capitalizeFirstLetter(item.title.toLocaleLowerCase())}
                </option>
              ))}
            </select>
          </div>
          {this.getClaimDataByTitle(
            this.ClaimTypes(this.FC_ClaimData(this.props.claiming.allClaims)!)
          ) && (
            <ExamDashboardLabels
              dataLabels={[
                {
                  title: "Total claim",
                  icon: FaBookOpen,
                  iconColor: "blue",
                  count: this.getClaimDataByTitle(
                    this.ClaimTypes(
                      this.FC_ClaimData(this.props.claiming.allClaims)!
                    )
                  )!.count.all,
                },
                {
                  title: "Accepted Claim",
                  icon: FaBookOpen,
                  iconColor: "green",
                  count: this.getClaimDataByTitle(
                    this.ClaimTypes(
                      this.FC_ClaimData(this.props.claiming.allClaims)!
                    )
                  )!.count.accepted,
                },
                {
                  title: "Rejected Claim",
                  icon: FaBookOpen,
                  iconColor: "yellow",
                  count: this.getClaimDataByTitle(
                    this.ClaimTypes(
                      this.FC_ClaimData(this.props.claiming.allClaims)!
                    )
                  )!.count.rejected,
                },
                {
                  title: "Pending Claim",
                  icon: FaBookOpen,
                  iconColor: "gray",
                  count: this.getClaimDataByTitle(
                    this.ClaimTypes(
                      this.FC_ClaimData(this.props.claiming.allClaims)!
                    )
                  )!.count.pending,
                },
              ]}
              labelsOnRow={5}
            >
              <div className="flex justify-center">
                <PieChart
                  options={{
                    labels: ["Accepted", "Rejected", "Pending"],
                    legend: {
                      show: false,
                      position: "bottom",
                      horizontalAlign: "center",
                      fontSize: "16px",
                      itemMargin: {
                        horizontal: 3,
                        vertical: 0,
                      },
                    },
                    plotOptions: {
                      pie: {
                        donut: {
                          labels: {
                            show: true,
                            name: {
                              show: true,
                              fontSize: "16px",
                              //   fontFamily: "Rubik",
                              color: "#2564eb",
                              offsetY: -10,
                            },
                            value: {
                              show: true,
                              fontSize: "20px",
                              fontFamily: "Helvetica, Arial, sans-serif",
                              color: "#2564eb",
                              offsetY: 10,
                              formatter: function (val) {
                                return val;
                              },
                            },
                            total: {
                              show: true,
                              label: "Total",
                              color: "#2564eb",
                              formatter: function (w) {
                                return w.globals.seriesTotals.reduce(
                                  (a: number, b: number) => {
                                    return a + b;
                                  },
                                  0
                                );
                              },
                            },
                          },
                        },
                      },
                    },
                    colors: ["#469769", "#d97724", "#4b5563"],
                  }}
                  series={[
                    this.getClaimDataByTitle(
                      this.ClaimTypes(
                        this.FC_ClaimData(this.props.claiming.allClaims)!
                      )
                    )!.count.accepted,
                    this.getClaimDataByTitle(
                      this.ClaimTypes(
                        this.FC_ClaimData(this.props.claiming.allClaims)!
                      )
                    )!.count.rejected,
                    this.getClaimDataByTitle(
                      this.ClaimTypes(
                        this.FC_ClaimData(this.props.claiming.allClaims)!
                      )
                    )!.count.pending,
                  ]}
                  width={210}
                />
              </div>
            </ExamDashboardLabels>
          )}
        </section>
        {/* CLAIM STATUS ON COUNTRY LEVEL LABELS ======================================= */}

        {/* ===================================================================================== */}

        {/* EXAM PERFORMANCE ON COUNTRY CHART ======================================= */}
        <AllClaimChart
          ClaimTypes={this.ClaimTypes(
            this.FC_ClaimData(this.props.claiming.allClaims)
          )}
        />
        {/* END EXAM PERFORMANCE ON COUNTRY CHART ======================================= */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
  claiming,
  document,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  claiming: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return {
    auth: auth,
    application_sessions: sessionPlan.allSessionPlans,
    systemPositions: system_settings.systemPositions,
    claiming,
    documentReducer: document,
    schoolLevels,
  };
};

export const ClaimDashboard = connect(mapStateToProps, {
  getAllClaims,
  GetClaimingTypes,
  getAllApplications,
  GetAllSessionPlans,
  loadEducationCategories,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  setSelectedClaimType,
  FC_GetPositionsByInstitution,
  PushPopupAlert,
})(_ClaimDashboard);
