import React from "react";
import DashboardLabelItem, {
  DashboardLabelItemInterface,
} from "../DashboardLabelItem";

type DashboardLabelProps = {
  dataLabels: DashboardLabelItemInterface[];
  labelsOnRow?: number;
  childrenPosition?: "START" | "END";
  children?: JSX.Element;
};

const ExamDashboardLabels: React.FC<DashboardLabelProps> = (props) => {
  const numCols: number = props.labelsOnRow
    ? props.labelsOnRow > 4
      ? props.labelsOnRow
      : 4
    : 4;
  return (
    <div
      className={`grid 2xl:grid-cols-${numCols} xl:grid-cols-${
        numCols - 1
      } lg:grid-cols-${numCols - 2} md:grid-cols-${
        numCols - 3
      } sm:grid-cols-2 grid-cols-1 gap-4 items-center mx-auto`}
    >
      {props.children &&
        ((props.childrenPosition && props.childrenPosition === "START") ||
          !props.childrenPosition) &&
        props.children}
      {props.dataLabels.map((item, index) => (
        <section key={index + item.title}>
          <DashboardLabelItem
            title={item.title}
            icon={item.icon}
            iconColor={item.iconColor}
            count={item.count}
          />
        </section>
      ))}
      {props.children &&
        props.childrenPosition &&
        props.childrenPosition === "END" &&
        props.children}
    </div>
  );
};

export default React.memo(ExamDashboardLabels);
